import React from 'react'

//CSS
import './tools.css'

export default function Tools() {
  return (
    <div className="toolsMainContainer" id="tools">

      <div className='toolsImage'>
        <img src="https://cdn4.iconfinder.com/data/icons/build-a-house-flat/512/construction_toolbox_repair_equipment_hammer_tool_box-512.png"></img>
      </div>

      <div className='toolsInformation'>
        <span className="toolsInfoTitle">We provide the tools necessary for businesses to save on their bottom line.</span>
        <p className="toolsInfoRegText">With decades of industry knowledge coupled with sophisticated technology and algorithms, we provide the best solutions for your business.</p>
      </div>


    </div>
  )
}
